import React, { Component } from 'react'

import { Card,Button,Table ,Tag,Modal,Typography, message} from 'antd';
import {getHandleCapsule,getHandleShare,getHandleArtifact} from '../../../services'
import {DownloadOutlined,UploadOutlined} from '@ant-design/icons'

import moment from 'moment'
import XLSX from 'xlsx'

import '../UserManageInfo/userManage.less'

const tittleDisplayMap = {
    capsule_id:'id',
    capsule_title:'',
    capsule_time:'',
    capsule_content:'',
    // artifact_info:'',
    // artifact_url:''
  
  //   author:'',
  //   createAt:'',
  //   amount:''
  }
const ButtonGroup = Button.Group;

export default class UserCapsules extends Component {
    constructor(){
        super()
        this.state= {
          showDataSource:[],
          dataSource:[],
          columns:[],
          isLoading:false,
          total:0,                    // 
          offset:0,
          limited:10,
          deleteArticleTitle:'',
          isShowArticleModal:false,
          deleteArticleConfirmLoading: false,
          currentDeleteArticleID:null,
        }
    }
    createColumns = (columnKeys)=>{
        const columns = columnKeys.map(item=>{
          if(item === 'capsule_time'){
            return{ 
              title:tittleDisplayMap[item],
              key:item,
              render : (record)=>{
                return moment(Number(record.capsule_time)).format('YYYY/MM/DD HH:mm')
              }
            }
          }
          if(item === 'capsule_content'){
            return{
              title:tittleDisplayMap[item],
              key:item,
              render : (record)=>{
                return <div dangerouslySetInnerHTML={{ __html: record.capsule_content }}  />
               
              }
            }
          }
          return{
            title:tittleDisplayMap[item],
            dataIndex:item,
            //  dataIndex  key   dataIndex   value
            //   render render   record 
            // record 
            key:item,
          }
        })
        columns.push({
          title:'',
          key:'action',
          Width: '100px',
          render:(record)=>{
            return (
              <div>
                <Button size='small' type="primary" onClick={this.toEdit.bind(this,record.capsule_id)} > </Button>
                <Button size="small" type="danger" onClick={this.showDeleteArticleModal.bind(this,record)}> </Button>
              </div>
            )
          }
        })
        return columns
    }
    deleteSomeKeys =  (columnKeys,keys)=>{
      let columns = columnKeys;
      for (let i = 0; i < keys.length; i++) {
        columnKeys.splice(columnKeys.indexOf(keys[i]),1)
      }
      return columns;
    }
    getData = ()=>{
        this.setState({
          isLoading:true
        })
        getHandleCapsule()
        .then(resp =>{
          let columnKeys = Object.keys(resp.data[0])
          columnKeys = this.deleteSomeKeys(columnKeys,['capsule_content'])
          const columns = this.createColumns(columnKeys);
          this.setState({
            dataSource:resp.data,
            showDataSource:resp.data,
            columns,
            total: resp.data.length,
          })
        }).catch(err=>{
        }).finally(err =>{
          this.setState({
            isLoading:false
          })
        })
      }
    
      toEdit=(id)=>{
        //  history.push    (state)  porps.location 
        //    
        this.props.history.push(`/admin/info/capsule/edit/:${id}`)
      }
      hideDeleteModal=()=>{
        this.setState({
          isShowArticleModal:false,
          deleteArticleTitle:'',
          deleteArticleConfirmLoading: false,
        })
      }
      showDeleteArticleModal = (record)=>{
        this.setState({
          isShowArticleModal:true,
          deleteArticleTitle: record.capsule_title,
          currentDeleteArticleID:record.capsule_id,
        })
      }
      deleteArticle= (id)=>{
        this.setState({
          deleteArticleConfirmLoading:true
        })
        // deleteArticleById(id)
        // .then(resp =>{
        //   message.success('')
        //   this.setState({
        //     offset:0
        //   },()=>{
        //     this.getData()
        //   })
        // })
        // .finally(resp =>{
        //   this.setState({
        //     deleteArticleConfirmLoading:false,
        //     isShowArticleModal:false
        //   })
        // })
      }

      componentDidMount(){
        this.getData()
      }
    onPageChange = (page,pageSize)=>{
      this.setState({
        offset:pageSize*(page-1),
        limited:pageSize
      },()=>{
        const infoStart = this.state.offset
        const infoEnd = (this.state.offset+this.state.limited+1)>this.state.dataSource.length ? this.state.dataSource.length : (this.state.offset+this.state.limited)
        const showData = this.state.dataSource.slice(infoStart,infoEnd)
        this.setState({
          showDataSource:showData
        })
      })
    }

    onShowSizeChange = (current,size)=>{
      this.setState({
        offset:0,
        limited:size
      },()=>{
        this.getData()
      })
    }

      toExcel = ()=>{
        const data =[Object.keys(this.state.dataSource[0])]
        for (let i = 0; i < this.state.dataSource.length; i++) {
          // const values = Object.values(this.state.dataSource[i])
            data.push([
              this.state.dataSource[i].capsule_id,
              this.state.dataSource[i].capsule_title,
              moment(this.state.dataSource[i].capsule_time).format('YYYY-MM-DD HH:mm'),
              // this.state.dataSource[i].capsule_content
              // moment(this.state.dataSource[i].createAt).format('YYYY-MM-DD HH:mm:ss')
            ])
        }
        const ws = XLSX.utils.aoa_to_sheet(data);
        const wb = XLSX.utils.book_new();
        XLSX.utils.book_append_sheet(wb, ws, "SheetJS");
        XLSX.writeFile(wb, `articlesAt${this.state.offset / this.state.limited + 1}-${moment().format('YYYYMMDDHHmmss')}.xlsx`)
      }

      toUpload = ()=>{
        this.props.history.push('/admin/info/capsule/upload')
      }

    render() {
        return (
            <Card  
            className="myUserCard"
            bordered={false} 
            extra={
              <div className="">
                <Button  type="primary" > </Button>
                  &emsp;
                <Button icon={<UploadOutlined />} shape="round" onClick={this.toUpload}> </Button>
                  &emsp;
                <Button icon={<DownloadOutlined />} onClick={this.toExcel}></Button>
              </div>
            } 
            pagination={{
                current:this.state.offset / this.state.limited +1,
                pageSize:1
            }}
        >
           <Table 
            loading={this.state.isLoading}
            rowKey = {record => record.capsule_id}
            dataSource={this.state.showDataSource} 
            columns={this.state.columns} 
            pagination={{
              current:this.state.offset / this.state.limited +1,    // current  
              total:this.state.total,
              hideOnSinglePage:false,          //  
              onChange: this.onPageChange,      //      
              showQuickJumper:true,
              showSizeChanger:true,
              onShowSizeChange:this.onShowSizeChange,       // pageSize    Function(current,size)
             }}

             style={{backgroundColor:'white'}}
             className="myUserTable"
            />
            <Modal
              size='small'
              title=''
              visible={this.state.isShowArticleModal}
              onCancel={this.hideDeleteModal}
              confirmLoading={this.state.deleteArticleConfirmLoading}
              onOk={this.deleteArticle}
            >
              <Typography>   <span style={{color:'#f00'}}> {this.state.deleteArticleTitle} </span> </Typography>
            </Modal>
          </Card>
        )
    }
}
